'use strict';

$(document).ready(function () {
    $('#fullpage').fullpage({
        anchors: ['Welcome', 'Page2', 'Page3', 'Work'],
        menu: '#menu',
        navigation: false,
        scrollOverflow: true,
        /*//*/loopTop: true

    });
});
